<template>
	<v-container class="mt-7">
		<v-row justify="center">
			<v-col md="5">
				<h2  class="mt-5">Login</h2>
				<v-form>
					<v-text-field v-model="user" label="email" type="email"></v-text-field>
					<v-text-field v-model="pass" label="password" type="password"></v-text-field>
					<v-btn @click="login" color="primary" block>Ingresar</v-btn>
				</v-form>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
import { getAuth, signInWithEmailAndPassword } from 'firebase/auth'
import {mapActions} from 'vuex'
export default {
	name: 'login-comp',
	// props: {},
	data: function () {
		return {
			user: '',
			pass: '',
		}
	},
	// computed: {},
	methods: {
        ...mapActions(['setToken']),
		login() {
			const auth = getAuth()
			signInWithEmailAndPassword(auth, this.user, this.pass)
				.then((userCredential) => {
					// Signed in
					const user = userCredential.user
					console.log(user)
                    this.setToken(user.accessToken)
					this.$router.push('/')
					// ...
				})
				.catch((error) => {
					const errorMessage = error.message
					alert(errorMessage)
				})
		},
	},
	// watch: {},
	// components: {},
	// mixins: [],
	// filters: {},
	// -- Lifecycle Methods
	// -- End Lifecycle Methods
}
</script>

<style scoped></style>
