<template>
	<div>
		<h1 class="text-center">Libro:{{ form.title }}</h1>

		<v-form v-model="valid" ref="form">
			<v-container>
				<v-row>
					<v-col>
						<v-switch
							v-model="allowEdit"
							label="Editar Libro"
							color="info"
							value="info"
							hide-details
						></v-switch>
					</v-col>
					<v-col cols="12" md="12">
						<v-text-field
							v-model="form.title"
							:rules="requiredRule"
							:counter="35"
							label="Nombre Libro"
							required
							:disabled="!allowEdit"
						></v-text-field>
					</v-col>

					<v-col cols="12" md="12">
						<v-text-field
							v-model="form.author"
							:rules="requiredRule"
							:counter="20"
							label="Autor"
							required
							:disabled="!allowEdit"
						></v-text-field>
					</v-col>
					<v-col cols="12" md="12">
						<v-text-field
							v-model="form.image"
							:rules="requiredRule"
							label="src"
							required
							:disabled="!allowEdit"
						></v-text-field>
					</v-col>
					<v-col cols="12" md="12">
						<v-select
							:items="types"
							label="Tipo"
							v-model="form.type"
							:rules="requiredRule"
							:disabled="!allowEdit"
						></v-select>
					</v-col>
					<v-col>
						<v-btn
							:disabled="!valid || !allowEdit"
							color="success"
							class="mr-4"
							@click="editBook"
						>
							Editar Libro
						</v-btn>
						<v-btn color="red" dark class="mr-4" @click="dialog=true">
							Eliminar
						</v-btn>
						<v-btn color="default" class="mr-4" @click="reset"> Limpiar </v-btn>

						<v-btn
							color="cyan"
							dark
							class="mr-4 my-2"
							block
							@click="$router.push('/')"
						>
							volver
						</v-btn>
					</v-col>
				</v-row>
			</v-container>
		</v-form>
		<v-dialog v-model="dialog" width="500">
			

			<v-card>
				<v-card-title class="text-h5 grey lighten-2">
					¿Desear eliminar el libro?
				</v-card-title>

				

				<v-divider></v-divider>

				<v-card-actions>
                    <v-btn color="red" text @click="deleteBook"> Eliminar</v-btn>
					<v-spacer></v-spacer>
					<v-btn color="primary" text @click="dialog = false"> Cerrar </v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</div>
</template>

<script>
import { mapState } from 'vuex'
import { db } from '@/firebase/firebase.js'
import { doc, getDoc, updateDoc,deleteDoc } from 'firebase/firestore'
export default {
	name: 'book-show-edit',
	props: ['idBook'],
	data: function () {
		return {
			form: {
				title: '',
				author: '',
				pages: '',
				type: '',
				image: '',
			},
			requiredRule: [
				(v) => !!v || 'Campo requerido',
				// v => v.length <= 10 || 'Name must be less than 10 characters',
			],
			valid: true,
			allowEdit: false,
            dialog:false
		}
	},
	computed: {
		...mapState(['types']),
	},
	methods: {
		// validate () {
		//     this.$refs.form.validate()
		// },
		reset() {
			this.$refs.form.reset()
		},
		async fetchBookById() {
			const docRef = doc(db, 'books', this.idBook)
			let response = await getDoc(docRef)
			console.log(response.data())
			this.form = response.data()
		},
		async editBook() {
			try {
				let book = this.form
				book.id = this.idBook
				console.log(book)
				let response = await updateDoc(doc(db, 'books', book.id), book)
				console.log(response)
				//alert('lIBRO ACTUALIZADOO')
				this.$router.push('/')
			} catch (error) {
				console.log(error)
			}
		},
        async deleteBook(){
            try{
                await deleteDoc(doc(db, "books", this.idBook));
                this.$router.push('/')
            }
            catch(error){
                console.log(error)
            }
            
        }
	},
	// watch: {},
	// components: {},
	// mixins: [],
	// filters: {},
	// -- Lifecycle Methods
	created() {
		this.fetchBookById()
	},
	// -- End Lifecycle Methods
}
</script>

<style scoped>
form {
	display: grid;
	width: 600px;
	margin: 0 auto;
}
</style>
